/**起钟、加钟、换种*/
<template>
  <div>
    <div class="content">
      <section class="selectKey">
        <div class="leftContent">
          <div
            v-for="(item,index) of this.getRespone.serviceKey"
            :key="index"
            @click="selectKey(item.id)"
            :class="['keyDiv',currentId == item.id?'actived':'']"
          >
            <div>
              <van-icon class="fireIco" v-show="index == 0" name="fire" color="#F2795B" size="20" />
              {{item.typeName}}
            </div>
          </div>
        </div>
      </section>
      <section class="selectVal">
        <div class="rightContent">
          <div v-for="(item,index) of serviceVals" :key="index" class="valDiv">
            <div v-for="(e,idx) of item.list" :key="idx" @click="selectVal(e.id,e)">
              <img :src="e.imgSrc" />
              <div class="rightInfo">
                <p>{{e.name}}</p>
                <p>时长:{{e.time}}(分钟)</p>
                <p>价格:￥{{e.price}}</p>
              </div>
              <van-icon v-show="currentItem == e.id" name="checked" color="#F2795B" size="18" />
            </div>
          </div>
        </div>
      </section>
    </div>
    <section class="bottomLine">
      <div class="btn" @click="czClick">重置</div>
      <div class="btn" @click="submitBtn">确认</div>
    </section>
  </div>
</template>

<script>
export default {
  data() {
    return {
      activeId: 0,
      activeIndex: 0,
      serviceVals: [],
      currentId: 1,
      currentItem: this.currentItem
    };
  },
  methods: {
    clickLeft(e) {
      this.activeId = 0;
    },
    selectKey(id) {
      this.currentId = id;
      this.serviceVals = [];
      this.getRespone.serviceVal.forEach(el => {
        el.list.forEach(e => {
          e["active"] = false;
          
        });
        if (this.currentId === el.id) {
            this.serviceVals.push(el);
          }
      });
    },
    selectVal(id, item) {
      this.currentItem = id;
    },
    submitBtn() {
      this.common_vant.openToast("起钟成功", "1000");
      setTimeout(() => {
        this.$router.go(-1);
      }, 1500);
    },
    czClick(){
      console.log('重置')
    }
  },
  mounted() {
    this.selectKey(this.currentId);
    this.selectVal(this.currentItem);

    var serviceKey = this.getRespone.serviceKey;
    var serviceVal = this.getRespone.serviceVal;
    serviceKey.forEach(item => {
      serviceVal.forEach((el, index) => {
        if (item.id == el.id) {
          serviceVal[index].text = item.typeName;
        }
        // el["children"] = el["list"];
        // el.children.forEach(elments => {
        //   elments["text"] = elments["name"];
        //   elments["id"] = elments["valId"];
        //   elments['active'] = false
        // });
      });
    });

    this.serviceVal = serviceVal;
    console.log(JSON.stringify(this.serviceVals));
  }
};
</script>

<style lang="scss" scoped>
.content {
  @include fRow(space-between, normal);
  @include wh(100%, auto);
  .selectKey {
    background-color: $F5;
    @include wh(30%, auto);
    .leftContent {
      .keyDiv {
        @include fc(center);
        @include sc(0.8rem, $fColor);
        padding: 0.8rem 0rem;
      }
      .actived {
        border-left: 0.2rem solid $orange;
        background: $fc;
      }
      div:last-child {
        @include fr(center);
        .fireIco {
          margin-right: 0.3rem;
        }
      }
    }
  }
  .selectVal {
    @include wh(70%, auto);
    padding: 0.8rem;
    .rightContent {
      @include fCol(flex-start, flex-start);
      @include wh(auto, auto);
      .valDiv {
        @include fc(flex-start);
        @include sc(0.8rem, $fColor);
        @include wh(auto, auto);
        div {
          @include fr(flex-start);
          margin: 0.4rem;
          @include wh(100%, auto);
          .rightInfo {
            @include fCol(flex-start, flex-start);
            @include wh(auto, auto);
            p {
              @include sc(0.8rem, $Tline);
            }
            p:first-child {
              @include sc(0.9rem, $fColor);
              font-weight: bold;
              margin-bottom: 0.2rem;
            }
          }
        }
      }
      img {
        @include wh(2.5rem, 2.5rem);
        border-radius: 5rem;
        margin-right: 0.5rem;
        border: 0.1rem solid $F5;
      }
    }
  }
}
.bottomLine {
  @include fr(space-between);
  @include wh(100%, auto);
  padding: 0.8rem 0.5rem;
  position: absolute;
  bottom: 2rem;
  .btn {
    @include wh(50%, auto);
    padding: 0.5rem;
    border-radius: 0.5rem;
    margin: 0.8rem;
  }
  .btn:first-child {
    @include sc(0.85rem, #589ffa);
    border: 0.1rem solid #589ffa;
  }
  .btn:last-child {
    @include sc(0.85rem, $fc);
    background-color: #589ffa;
    background-image: linear-gradient(
      180deg,
      #589ffa 0%,
      #6e95f3 50%,
      #7a90ef 100%
    );
  }
}
</style>